.account-custom {
  max-width: 1286px;
  margin: 0 auto;
  width: 100%;
  font-family: 'AvenirNextCyr';
  @media screen and (max-width: 1365px) {
    padding: 0 15px;
  }
  &__holder {
    display: flex;
    @media screen and (max-width: 1250px) {
      flex-wrap: wrap;
    }
  }
  &__title {
    color: #1b1b1a;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 50px;
    margin-bottom: 20px;
    svg {
      display: block;
    }
  }
  &__aside {
    max-width: 299px;
    flex-basis: 299px;
    width: 100%;
    flex-shrink: 0;
    @media screen and (max-width: 1250px) {
      max-width: 100%;
      flex-basis: 100%;
      margin-bottom: 30px;
      overflow-x: auto;
    }
  }
  &__content-title {
    color: #1b1b1a;
    display: flex;
    align-items: center;
    font-size: 20px;
    font-style: normal;
    font-weight: 450;
    line-height: normal;
    svg {
      stroke: #e34200;
      margin-right: 8px;
    }
  }
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    border: 1px solid #e7e4e7;
    @media screen and (max-width: 1250px) {
      display: flex;
      justify-content: space-between;
    }
    li {
      @media screen and (max-width: 1250px) {
        flex-shrink: 0;
      }
      & + li {
        border-top: 1px solid #e7e4e7;
      }
      &.active {
        a {
          background: #fff3f3;
          font-weight: 700;
          &::after {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            width: 3px;
            background: #e34200;
          }
          svg {
            stroke: #e34200;
          }
        }
      }
    }
    a {
      text-decoration: none;
      padding: 0 16px;
      height: 52px;
      display: flex;
      align-items: center;
      color: #1b1b1a;
      font-size: 16px;
      font-style: normal;
      position: relative;
      font-weight: 400;
      line-height: 24px;
      @media screen and (min-width: 1250px) {
        &:hover {
          color: #e34200;
        }
      }
      svg {
        margin-right: 8px;
        display: block;
        stroke: #828282;
      }
    }
  }
  &__content {
    width: 100%;
    margin-left: 28px;
    @media screen and (max-width: 1250px) {
      margin: 0;
    }
  }
  &__address-section {
    border-top: 1px solid #e7e4e7;
  }
  &__address-box {
    border-bottom: 1px solid #e7e4e7;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 0;
  }
  &__address-info {

  }
  &__address-main {
    color: #828282;
    font-size: 14px;
    font-style: normal;
    font-weight: 450;
    line-height: normal;
    padding-left: 16px;
    position: relative;
    margin-bottom: 8px;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 5px;
      background: #e34200;
      width: 8px;
      height: 8px;
      border-radius: 50%;
    }
  }
  &__town {
    color: #1b1b1a;
    font-size: 14px;
    font-style: normal;
    font-weight: 450;
    line-height: normal;
    margin-bottom: 4px;
  }
  &__np {
    color: #1b1b1a;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 4px;
  }
  .basket__col {
    svg {
      position: absolute;
      right: 16px;
      top: 17px;
      pointer-events: none;
    }
  }
  &__address-control {
    display: flex;
    align-items: center;
  }
  &__address-edit {
    display: flex;
    align-items: center;
    color: #0db14b;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    svg {
      margin-right: 6px;
    }
  }
  &__address-delete {
    color: #e34200;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    margin-left: 24px;
    svg {
      margin-right: 6px;
    }
  }
  &__products {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .card__hidden {
      height: auto;
      position: relative;
    }
    .card__info {
      position: relative;
    }
    @media screen and (max-width: 767px) {
      .card-item {
        max-width: 172px;
      }
    }
  }
}
.account-delete-prod {
  background: none;
  padding: 0;
  border: none;
  position: absolute;
  bottom: 7px;
  right: 0;
  color: #828282;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  display: flex;
  align-items: center;
  cursor: pointer;
  @media screen and (max-width: 767px) {
    right: 3px;
  }
  svg {
    margin-right: 8px;
    @media screen and (max-width: 767px) {
      margin-right: 4px;
    }
  }
}
.pass-edit {
  color: #1b1b1a;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}
.add-address {
  color: #1b1b1a;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  cursor: pointer;
  background: none;
  padding: 0;
  border: none;
  text-decoration: underline;
  &:hover {
    text-decoration: none;
  }
}
.orders-history {
  &__wrap {
    margin-bottom: 10px;
  }
  &__block {
    @media screen and (max-width: 1250px) {
      margin: 0 auto;
    }
  }
  &__col {
    display: flex;
    align-items: center;
    @media screen and (max-width: 767px) {
      flex-wrap: wrap;
      & + .orders-history__col {
        width: 100%;
      }
    }
  }
  &__head {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    @media screen and (max-width: 767px) {
      display: none;
    }
  }
  &__head-item {
    width: 25%;
    color: #828282;
    font-size: 14px;
    font-style: normal;
    font-weight: 450;
    line-height: normal;
  }
  &__custom {
    width: 25%;
    color: #1b1b1a;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    &.wait {
      color: #e3a300;
    }
    &.canceled {
      color: #e34200;
    }
    &.done {
      color: #0db14b;
    }
  }
  &__item {
    border: 1px solid #e4e4e4;
    border-radius: 8px;
    margin-bottom: 30px;
    &.active {
      .orders-history__title {
        background: #f9f8f9;
      }
      .orders-history__icon span {
        background: #fff;
        &::after {
          background: #ef2d8a;
        }
        &::before {
          display: none;
        }
      }
      .orders-history__content {
        background: #f9f8f9;
      }
    }
  }
  .basket__quantity input {
    background: #f9f8f9;
  }
  .basket__item {
    border-width: 1px 0 0;
  }
  .basket__item + .basket__item {
    margin: 0;
  }
  &__cus-info {
    display: flex;
    padding: 16px 0;
    @media screen and (max-width: 767px) {
      flex-wrap: wrap;
    }
  }
  &__cus-name {
    font-weight: 700;
    margin-bottom: 12px;
  }
  &__cus-col {
    width: 33%;
    color: #1b1b1a;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    padding-right: 30px;
    @media screen and (max-width: 767px) {
      width: 100%;
      & + .orders-history__cus-col {
        margin-top: 20px;
      }
    }
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      li {
        & + li {
          margin-top: 8px;
        }
      }
    }
  }
  &__title {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 56px;
    cursor: pointer;
    padding: 0 0 0 40px;
    position: relative;
    @media screen and (max-width: 767px) {
      flex-wrap: wrap;
      height: auto;
      padding: 16px 50px 16px 40px;
    }
    &::after {
      border-bottom: 1px solid #121315;
      border-right: 1px solid #121315;
      width: 8px;
      height: 8px;
      left: 18px;
      margin-top: -4px;
      content: '';
      display: block;
      pointer-events: none;
      position: absolute;
      top: 50%;
      -webkit-transform-origin: 66% 66%;
      transform-origin: 66% 66%;
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
      -webkit-transition: all 0.15s ease-in-out;
      transition: all 0.15s ease-in-out;
    }
    &.orders-history__success {
      &::after {
        background: #d6e7a6;
      }
      .orders-history__status {
        background: #d6e7a6;
      }
    }
  }
  &__number {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
  }
  &__date {
    color: #5b5b5b;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    padding: 0 8px;
  }
  &__status {
    color: #000;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 16px;
    height: 20px;
    padding: 0 8px;
    display: flex;
    align-items: center;
    border-radius: 4px;
    @media screen and (max-width: 767px) {
      margin: 4px 0 8px;
    }
  }
  &__invoice {
    color: #5b5b5b;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
  }
  &__price {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
    margin-left: 8px;
    @media screen and (max-width: 767px) {
      width: 100%;
      margin: 0 0 4px;
    }
  }
  &__icon {
    position: relative;
    width: 50px;
    height: 50px;
    flex-shrink: 0;
    background: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 14px;
    @media screen and (max-width: 767px) {
      margin: 0;
      position: absolute;
      top: 2px;
      right: 0;
    }
    span {
      width: 25px;
      height: 25px;
      position: relative;
      &::after {
        content: '';
        position: absolute;
        left: 50%;
        top: 50%;
        width: 14px;
        height: 1px;
        background: #000;
        transform: translate(-50%,0%);
      }
      &::before {
        content: '';
        position: absolute;
        left: 50%;
        top: 50%;
        width: 1px;
        height: 14px;
        background: #000;
        transform: translate(0%,-50%);
      }
    }
  }
  &__price {
    &::after {
      display: none;
    }
  }
  &__inner {
    height: 0;
    overflow: hidden;
    transition: .3s ease-in-out;
  }
  &__content {
    border-top: 1px solid transparent;
    padding: 0 16px;
  }
  &__box {
    padding: 0 0 19px;
  }
  &__bottom {
    padding: 16px 0;
    border-top: 1px solid #e4e4e4;
  }
  &__bottom-title {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
    margin-bottom: 12px;
  }
  &__box-value {
    color: #000;
    text-align: right;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    width: 50%;
  }
  &__load {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f5f7fa;
    line-height: 36px;
    font-size: 16px;
    color: #485860;
    text-decoration: none;
    border-radius: 30px;
    svg {
      margin-right: 12px;
      width: 19px;
      height: 19px;
    }
  }
  &__box-title {
    font-size: 15px;
    line-height: 130%;
    color: #52636c;
    margin-bottom: 16px;
  }
  &__box-list {
    margin:0;
    padding:0;
    list-style: none;
  }
  &__box-item {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    & + .orders-history__box-item {
      margin-top: 12px;
    }
    .text {
      color: #5b5b5b;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 22px;
      &.black {
        color: #000;
      }
    }
    .dots {
      height: 1px;
      width: 100%;
      border-bottom: 1px dotted #d0dde4;
      margin: 0 15px 4px;
    }
    .value {
      color: #000;
      text-align: right;
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: 22px;
      &.big {
        font-size: 20px;
        line-height: 28px;
      }
    }
  }
  &__box-text {
    font-size: 15px;
    line-height: 130%;
    color: #52636c;
  }
  &__about {
    .orders-history__box-item {
      .text {
        font-size: 14px;
      }
      .value {
        font-size: 15px;
      }
      &:last-of-type {
        .text {
          color: #3d3d3d;
          font-size: 16px;
        }
        .value {
          color: #3d3d3d;
          font-size: 18px;
        }
      }
    }
  }
  &__bootom {
    display: flex;
    justify-content: flex-end;
    padding-bottom: 40px;
  }
  &__bottom-error {
    font-size: 15px;
    line-height: 170%;
    color: #52636c;
    margin-right: 24px;
    span {
      color: #000;
    }
  }
  &__buy {
    display: flex;
    max-width: 108px;
    width: 100%;
    height: 34px;
    background: red;
    border: 1px solid red;
    box-shadow: 0px 10px 21px rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    font-size: 12px;
    color: #fff;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;
    transition: .3s ease-in-out;
  }
  &__status {
    span {
      margin: 0 auto;
      display: flex;
      width: 105px;
      height: 26px;
      background: #fbeaea;
      border-radius: 8px;
      font-size: 14px;
      line-height: 130%;
    }
  }
}
.order {
  &__vendor {
    font-size: 12px;
    line-height: 15px;
    color: rgba(0,0,0,0.4);
    margin-bottom: 10px;
  }
  &__list {
    margin-bottom: 30px;
    border-bottom: 1px solid #e4e4e4;
  }
  &__item {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 16px 0;
    border-bottom: 1px solid #e4e4e4;
    @media screen and (max-width: 767px) {
      flex-wrap: wrap;
    }
    &:last-of-type {
      border: none;
    }
  }
  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 18px;
  }
  &__image {
    max-width: 67px;
    width: 100%;
    margin-right: 16px;
  }
  &__item-block {
    display: flex;
    align-items: center;
  }
  &__item-left {
    max-width: 440px;
    width: 100%;
    @media screen and (max-width: 767px) {
      margin-bottom: 16px;
    }
  }
  &__content {
    max-width: 335px;
    width: 100%;
  }
  &__article {
    font-size: 12px;
    color: #a2a2a2;
    margin-bottom: 10px;
  }
  &__title {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    a {
      color: #000;
      text-decoration: none;
      transition: .3s ease-in-out;
    }
  }
  &__author {
    font-size: 14px;
    line-height: 150%;
    color: #8b9ca6;
    margin-bottom: 15px;
  }
  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info-item {

  }
  &__info-right {
    display: flex;
    .order__info-item + .order__info-item {
      margin-left: 44px;
    }
  }
  &__info-text {
    color: #a6a6a6;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    margin-bottom: 8px;
  }
  &__info-value {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
    min-height: 68px;
    display: flex;
    align-items: center;
  }
  &__btn {
    border: 2px solid rgba(29,88,158,0.2);
    border-radius: 30px;
    display: flex;
    align-items: center;
    background: #fff;
    max-width: 160px;
    flex-basis: 160px;
    justify-content: center;
    flex-shrink: 0;
    width: 100%;
    padding: 0 21px 0 14px;
    font-weight: bold;
    font-size: 12px;
    line-height: 34px;
    text-transform: uppercase;
    font-family: 'Calibri';
    color: #1d589e;
    cursor: pointer;
    svg {
      fill: #1d589e;
      width: 18px;
      height: 18px;
      margin-right: 7px;
    }
  }
}
.popup-custom {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  align-items: center;
  justify-content: center;
  &.show {
    display: flex;
  }
  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.7);
  }
  &__holder {
    background: #fff;
    position: relative;
    z-index: 1;
    max-width: 537px;
    width: 100%;
    padding: 40px;
    font-family: 'AvenirNextCyr';
    .basket__col {
      max-width: 100%;
      position: relative;
    }
  }
  .toggle-password {
    position: absolute;
    right: 20px;
    top: 18px;
  }
  &__sub-title {
    color: #1b1b1a;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
    margin-bottom: 24px;
  }
  &__btn {
    background: #e34200;
    border: 1px solid #e34200;
    height: 56px;
    width: 100%;
    color: #fff;
    text-align: center;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 16px;
    cursor: pointer;
    text-transform: uppercase;
    margin-top: 24px;
    @media screen and (min-width: 1250px) {
      &:hover {
        background: #fff;
        color: #000;
      }
    }
  }
  &__or {
    color: #1b1b1a;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 450;
    line-height: 14px;
    margin: 20px 0;
  }
  &__add {
    background: #1b1b1a;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 48px;
    color: #fff;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 16px;
    border: none;
    svg {
      margin-right: 12px;
    }
  }
  &__title {
    color: #1b1b1a;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 24px;
  }
  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
  }
}
.tel-again {
  color: #1b1b1a;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px; /* 100% */
  text-decoration: underline;
  margin-bottom: 20px;
  display: block;
  text-align: center;
  width: 100%;
  background: none;
  padding: 0;
  border: none;
  margin-top: 20px;
  @media screen and (min-width: 1250px) {
    &:hover {
      text-decoration: none;
    }
  }
}
.tel-edit {
  color: #1b1b1a;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px; /* 100% */
  text-decoration: underline;
  margin-bottom: 20px;
  display: block;
  text-align: center;
  width: 100%;
  background: none;
  padding: 0;
  border: none;
  @media screen and (min-width: 1250px) {
    &:hover {
      text-decoration: none;
    }
  }
}
