* {
  &::after,
  &::before {
    box-sizing: border-box;
  }
  box-sizing: border-box;
}
body,
html {
  height: 100%;
}
body {
  margin: 0;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
  background: #eceffa;
  color: #000;
  min-width: 320px;
  font-family: 'PN';
}
svg {
  transform: translateZ(0);
}
button {
  font-family: 'PN';
  cursor: pointer;
}
button:focus,
input:focus,
textarea:focus {
  outline: none;
}
button[disabled],
input[disabled],
textarea[disabled] {
  pointer-events: none;
  opacity: 0.4;
  background: #999;
  border-color: #999;
  @media screen and (max-width: 1250px) {
    opacity: 1;
  }
}
input[type="text"],
input[type="tel"],
input[type="email"],
input[type="search"],
input[type="password"] {
  background: #F9F8F9;
  border: 1px solid #828282;
  box-sizing: border-box;
  border-radius: 0;
  width: 100%;
  height: 56px;
  font-size: 16px;
  line-height: 18px;
  color: #343434;
  display: block;
  padding: 0;
  box-shadow: none;
  appearance: none;
  font-family: 'PN';
  padding: 0 16px;
  &.error {
    border-color: #f00;
  }
  &.success {
    border-color: #4ce163;
  }
}
.error input[type="text"],
.error input[type="tel"],
.error input[type="email"],
.error input[type="search"],
.error input[type="password"],
.error .iti--separate-dial-code .iti__selected-flag {
  border-color: #f00;
}
.success input[type="text"],
.success input[type="tel"],
.success input[type="email"],
.success input[type="search"],
.success input[type="password"],
.success .iti--separate-dial-code .iti__selected-flag {
  border-color: #4ce163;
}

textarea {
  background: none;
  border: solid rgba(212,212,212,0.24);
  border-width: 0 0 1px;
  box-sizing: border-box;
  border-radius: 0;
  width: 100%;
  height: 54px;
  resize: none;
  padding: 10px 0 5px;
  display: block;
  box-shadow: none;
  appearance: none;
  font-size: 16px;
  line-height: 18px;
  color: #343434;
  font-family: 'PN';
  &:focus {
    border-color: #879d04;
  }
  &.error {
    border-color: #f00;
  }
  &.success {
    border-color: #4ce163;
  }
}
p.error {
  font-size: 13px;
  line-height: 15px;
  color: red;
}
:-moz-placeholder {
  color: #828282;
}
::-moz-placeholder {
  color: #828282;
  opacity: 1;
}
:-ms-input-placeholder {
  color: #828282;
}
::-webkit-input-placeholder {
  color: #828282;
}
input:focus::-webkit-input-placeholder {
  color: transparent;
}
input:focus:-moz-placeholder {
  color: transparent;
}
input:focus::-moz-placeholder {
  color: transparent;
}
textarea:focus::-webkit-input-placeholder {
  color: transparent;
}
textarea:focus:-moz-placeholder {
  color: transparent;
}
textarea:focus::-moz-placeholder {
  color: transparent;
}
::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}
::selection {
  background: #b3d4fc;
  text-shadow: none;
}
::-ms-clear {
  display: none;
}
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}
.center-text {
  text-align: center;
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
a {
  color: #1cb531;
  text-decoration: underline;
  outline: none;
  cursor: pointer;
}
a:hover {
  text-decoration: none;
}
a:focus {
  outline: none;
}
.wrapper {
  width: 100%;
}
.header {
  background: #2a1468;
  padding: 9px 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 26px;
  @media screen and (max-width: 1250px) {
    padding: 9px 15px;
  }
}
.mobile-opener {
  display: none;
  padding: 8px 10px;
  color: #fff;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 140%;
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.05);
  @media screen and (max-width: 1250px) {
    display: flex;
    align-items: center;
  }
  &__lines {
    margin-left: 4px;
  }
  &__line-1 {
    width: 14px;
    height: 1px;
    border-radius: 4px;
    background: #fff;
    margin-bottom: 3px;
    display: block;
  }
  &__line-2 {
    width: 14px;
    height: 1px;
    border-radius: 4px;
    background: #fff;
    margin-bottom: 3px;
    display: block;
  }
  &__line-3 {
    width: 10px;
    height: 1px;
    border-radius: 4px;
    background: #fff;
    display: block;
  }
}
.nav-close {
  display: none;
  position: absolute;
  right: 10px;
  top: 15px;
  display: none;
  align-items: center;
  justify-content: center;
  width: 34px;
  height: 34px;
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.05);
  @media screen and (max-width: 1250px) {
    display: flex;
  }
}
.nav {
  @media screen and (max-width: 1250px) {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    background: #2A1468;
    box-shadow: -10px 4px 44px 0px rgba(78, 72, 72, 0.25);
    width: 286px;
    height: 100vh;
    z-index: 10;
    padding-top: 98px;
    &.show {
      display: block;
    }
  }
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    color: #eceffa;
    font-family: 'PN';
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    @media screen and (max-width: 1250px) {
      display: block;
    }
    li {
      & + li {
        margin-left: 24px;
        @media screen and (max-width: 1250px) {
          margin: 8px 0 0;
        }
      }
      &.active {
        a {
          color: #f3C82e;
        }
      }
    }
    a {
      color: #eceffa;
      text-decoration: none;
      -webkit-transition: all 0.15s ease-in-out;
      transition: all 0.15s ease-in-out;
      @media screen and (min-width: 1250px) {
        &:hover {
          color: #f3C82e;
        }
      }
      @media screen and (max-width: 1250px) {
        display: block;
        padding: 10px 16px;
      }
    }
  }
}
.twocolumns {
  display: flex;
  align-items: flex-start;
  max-width: 1110px;
  margin: 0 auto 80px;
  @media screen and (max-width: 1250px) {
    padding: 0 15px;
  }
  @media screen and (max-width: 767px) {
    flex-wrap: wrap;
  }
}
.content {
  max-width: 806px;
  width: 100%;
  margin-right: 24px;
  background: #fff;
  padding: 40px 32px;
  border-radius: 16px;
  color: #333;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  @media screen and (max-width: 767px) {
    margin: 0;
    order: 1;
    padding: 24px 16px;
  }
  h1 {
    color: #101010;
    font-size: 44px;
    font-style: normal;
    font-weight: 500;
    line-height: 100%;
    margin: 20px 0;
    @media screen and (max-width: 767px) {
      font-size: 32px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  p {
    margin: 20px 0;
  }
  img {
    margin: 20px 0 60px;
    display: block;
  }
  h2 {
    color: #333;
    font-size: 32px;
    font-style: normal;
    font-weight: 500;
    line-height: 100%;
    margin: 20px 0;
    @media screen and (max-width: 767px) {
      font-size: 24px;
    }
  }
  strong {
    color: #333;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
  }
  ul {
    margin: 14px 0;
    padding: 0;
    list-style: none;
    li {
      padding-left: 20px;
      position: relative;
      & + li {
        margin-top: 14px;
      }
      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 8px;
        width: 8px;
        height: 8px;
        background: #333;
        border-radius: 50%;
      }
    }
    strong {
      display: block;
      margin-bottom: 8px;
    }
    p {
      margin: 0;
    }
  }
  ol {
    padding-left: 20px;
    margin: 20px 0;
    line-height: 160%;
  }
  h3 {
    color: #333;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    margin: 20px 0;
  }
}
.aside {
  max-width: 280px;
  width: 100%;
  background: #fff;
  position: sticky;
  top: 0;
  padding: 36px 24px;
  border-radius: 16px;
  @media screen and (max-width: 1023px) {
    max-width: 230px;
  }
  @media screen and (max-width: 767px) {
    max-width: 100%;
    padding: 14px 16px;
    position: relative;
    margin-bottom: 14px;
  }
  &__title {
    color: #101010;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    margin-bottom: 16px;
    @media screen and (max-width: 767px) {
      position: relative;
      margin: 0;
      font-size: 18px;
      &::after {
        border-bottom: 1px solid #101010;
        border-right: 1px solid #101010;
        content: '';
        display: block;
        width: 7.5px;
        height: 7.5px;
        right: 4px;
        margin-top: -5px;
        pointer-events: none;
        position: absolute;
        top: 50%;
        -webkit-transform-origin: 66% 66%;
        transform-origin: 66% 66%;
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        -webkit-transition: all 0.15s ease-in-out;
        transition: all 0.15s ease-in-out;
      }
    }
    &.active::after {
      transform: rotate(-135deg);
    }
  }
  &__nav {
    list-style-type: none;
    counter-reset: custom-counter;
    padding-left: 0;
    margin: 0;
    color: #101010;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    @media screen and (max-width: 767px) {
      display: none;
      &.show {
        display: block;
        padding-top: 10px;
      }
    }
   li {
      counter-increment: custom-counter;
      display: flex;
      align-items: flex-start;
      & + li {
        margin-top: 10px;
      }
      &::before {
         content: counter(custom-counter, decimal-leading-zero) "";
         width: 22px;
         text-align: center;
         flex-shrink: 0;
         margin-right: 6px;
      }
      &.active {
        a {
          font-weight: 500;
          text-decoration: underline;
        }
      }
      a {
        text-decoration: none;
        color: #101010;
        -webkit-transition: all 0.15s ease-in-out;
        transition: all 0.15s ease-in-out;
        @media screen and (min-width: 1250px) {
          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }
}
.footer-nav {
  @media screen and (max-width: 767px) {
    width: 100%;
  }
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    color: #eceffa;
    font-family: 'PN';
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    @media screen and (max-width: 767px) {
      display: block;
    }
    li {
      & + li {
        margin-left: 24px;
        @media screen and (max-width: 1023px) {
          margin-left: 14px;
        }
        @media screen and (max-width: 767px) {
          margin: 24px 0 0;
        }
      }
      &.active {
        a {
          color: #f3C82e;
        }
      }
    }
    a {
      color: #101010;
      text-decoration: none;
      -webkit-transition: all 0.15s ease-in-out;
      transition: all 0.15s ease-in-out;
      @media screen and (min-width: 1250px) {
        &:hover {
          color: #f3C82e;
        }
      }
    }
  }
}
.footer {
  background: #fff;
  &__holder {
    max-width: 1110px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 60px 0 48px;
    @media screen and (max-width: 1250px) {
      padding: 60px 15px 48px;
    }
    @media screen and (max-width: 1023px) {
      flex-wrap: wrap;
      .logo {
        margin-bottom: 27px;
      }
    }
  }
  &__copy {
    border-top: 1px solid #eceffa;
    padding: 28px 15px;
    color: #6f7897;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    text-align: center;
  }
}
.faq-section {
  max-width: 1110px;
  width: 100%;
  margin: 0 auto 80px;
  @media screen and (max-width: 1250px) {
    padding: 0 15px;
  }
}
.faq-box {
  max-width: 806px;
  width: 100%;
}
.faq {
  margin: 0;
  padding: 0;
  list-style: none;
  .opener {
    color: #181818;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    text-decoration: none;
    display: block;
    position: relative;
    padding-right: 30px;
    &::after {
      content: '';
      position: absolute;
      right: 0;
      top: 11px;
      width: 18px;
      height: 2px;
      background: #6f7897;
    }
    &::before {
      content: '';
      position: absolute;
      right: 8px;
      top: 3px;
      width: 2px;
      height: 18px;
      background: #6f7897;
    }
  }
  .slide {
    color: #4b4570;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    padding-top: 8px;
    p {
      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  li {
    background: #fff;
    padding: 20px 32px;
    border-radius: 10px;
    & + li {
      margin-top: 10px;
    }
    &.active {
      .opener {
        &::before {
          transform: rotate(45deg);
        }
        &::after {
          transform: rotate(-135deg);
        }
      }
    }
  }
}
.faq-title {
  color: #181818;
  font-size: 32px;
  font-style: normal;
  font-weight: 800;
  line-height: 110%;
  margin-bottom: 16px;
  @media screen and (max-width: 1250px) {
    font-size: 24px;
  }
}
