@mixin font($fontname, $filename, $weight, $style) {
  @font-face {
    font-family: $fontname;
    src:
      url(#{'../fonts/'+$filename+'.woff2'}) format("woff");
    font-style: $style;
    font-weight: $weight;
    font-display: fallback;
  }
}

@include font('PN', 'ProximaNova-Regular', 400, normal);
@include font('PN', 'ProximaNova-Semibold', 500, normal);
@include font('PN', 'ProximaNova-Bold', 700, normal);
@include font('PN', 'ProximaNova-Black', 900, normal);
